.grid-container {
  display: grid;
  grid-template-columns: 48vw 50vw ;
}

.grid-item {
    font-size: 30px;
    text-align: center;
}

.left{
  background-color: #bbdefb;
}

.right{
  background-color: #ABCCE8 ;
}

@media (min-width: 768px) and (max-width: 1024px) {
  
  .grid-container {
    display: grid;
    grid-template-columns: 95vw ;
  }
}

@media (max-width: 767px) {
  
  .grid-container {
    display: grid;
    grid-template-columns: 95vw ;
  }
}